.slider{
    height: 730px;
    margin: auto;
    position: relative;
    top: 5%;
    width: 98%;
    display: flex;
    place-items: center;
    overflow: hidden;
}
.slideTrack{
    display: flex;
    width: calc(800px * 12);
    margin: auto;
    animation: scroll 40s linear infinite;
}
.slideTrack:hover{
    animation-play-state: paused;
}
@keyframes scroll {
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(calc(-700px*6));
    }
}
.slide{
    height: 720px;
    width: 700px;
    align-items: center;
    padding: 2px;
}
.slide img{
    height: 350px;
    width: 95%;
}

.content{
    display: flex;
}
.column{
    display: flex;
}

@media screen and (max-width: 768px) {
    main,
    .slide {
        height: 300px;
        width: 300px;
    }
    .slideTrack{
        width: calc(300px * 12);
    }
    @keyframes scroll {
        0%{
            transform: translateX(0);
        }
        100%{
            transform: translateX(calc(-300px*6));
        }
    }
    .column{
        flex-direction: column;
    }
  }